* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #d3ee5b;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}
ul {
  list-style: none;

  
}
li{
  display: flex;
  align-items: center;
  font-size: 20px;
  font-style: normal;
  margin-bottom: 10px;
}
span {
  margin-left: 15px;
}
div {
  text-align: center;
}

p {
  font-size: 20px;
  font-style: normal;
}